.analytics-page {
  padding: 40px;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  min-height: 100vh;
  color: white;
  font-family: Inter, sans-serif;
}

.kpi-grid {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
}

.kpi-card {
  flex: 1;
  background: rgba(255,255,255,0.05);
  padding: 20px;
  border-radius: 15px;
  text-align: center;
}

.kpi-card p {
  font-size: 28px;
  margin: 10px 0 0 0;
}

.chart-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.chart-card {
  background: rgba(255,255,255,0.05);
  padding: 20px;
  border-radius: 15px;
}
