/* PAGE BACKGROUND */
.login-page {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  font-family: Inter, sans-serif;
}

/* GLASS CARD */
.login-card {
  width: 380px;
  padding: 40px;
  border-radius: 20px;
  background: rgba(255,255,255,0.05);
  backdrop-filter: blur(20px);
  box-shadow: 0 20px 60px rgba(0,0,0,0.5);
  color: white;
  animation: fadeIn 0.6s ease;
}

/* BRAND */
.brand {
  margin: 0;
  font-size: 28px;
  text-align: center;
  margin-bottom: 6px;
}

.subtitle {
  text-align: center;
  font-size: 13px;
  opacity: 0.7;
  margin-bottom: 30px;
}

/* INPUT GROUP */
.input-group {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;
}

.input-group label {
  font-size: 13px;
  margin-bottom: 6px;
  opacity: 0.8;
}

.input-group input {
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid rgba(255,255,255,0.15);
  background: rgba(255,255,255,0.05);
  color: white;
  outline: none;
  transition: 0.3s;
}

.input-group input:focus {
  border: 1px solid #3b82f6;
  box-shadow: 0 0 10px rgba(59,130,246,0.5);
}

/* BUTTON */
button {
  width: 100%;
  padding: 12px;
  border-radius: 12px;
  border: none;
  background: linear-gradient(135deg, #3b82f6, #2563eb);
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: 0.3s;
}

button:hover {
  opacity: 0.9;
}

button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* ERROR */
.error {
  color: #ef4444;
  font-size: 13px;
  margin-bottom: 15px;
  text-align: center;
}

/* ANIMATION */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
