/* ================= GLOBAL ================= */

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  background: linear-gradient(135deg, #0f172a, #1e293b);
  color: white;
}

.layout {
  display: flex;
  min-height: 100vh;
}

/* ================= SIDEBAR ================= */

.sidebar {
  width: 240px;
  background: linear-gradient(180deg, #0f172a, #111827);
  padding: 30px 20px;
  display: flex;
  flex-direction: column;
  gap: 12px; /* Reduced spacing */
  min-height: 100vh;
}

.logo {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 25px;
}

/* Nav Buttons */

.nav {
  padding: 12px 16px;
  border-radius: 10px;
  background: rgba(255,255,255,0.05);
  border: none;
  color: #e5e7eb;
  cursor: pointer;
  text-align: left;
  font-weight: 500;
  transition: all 0.2s ease;
}

.nav:hover {
  background: rgba(255,255,255,0.1);
}

.nav.active {
  background: linear-gradient(90deg, #3b82f6, #2563eb);
  color: white;
}

/* Analytics Link */

.nav-link {
  padding: 12px 16px;
  border-radius: 10px;
  background: rgba(255,255,255,0.05);
  color: #e5e7eb;
  text-decoration: none;
  display: block;
  font-weight: 500;
  transition: all 0.2s ease;
}

.nav-link:hover {
  background: rgba(255,255,255,0.1);
}

/* ================= MAIN CONTENT ================= */

.content {
  flex: 1;
  padding: 40px 60px;
}

.title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 30px;
}

/* ================= DATE FILTER ================= */

.date-filter {
  display: flex;
  gap: 15px;
  margin-bottom: 20px;
}

.date-filter input {
  padding: 8px 12px;
  border-radius: 8px;
  border: none;
  background: rgba(255,255,255,0.1);
  color: white;
}

/* ================= KPI CARDS ================= */

.kpi-row {
  display: flex;
  gap: 20px;
  margin-bottom: 30px;
}

.kpi-card {
  flex: 1;
  background: rgba(255,255,255,0.05);
  padding: 25px;
  border-radius: 14px;
  text-align: center;
  backdrop-filter: blur(10px);
}

.kpi-card h4 {
  margin: 0;
  font-weight: 500;
  color: #cbd5e1;
}

.kpi-card p {
  margin: 10px 0 0;
  font-size: 28px;
  font-weight: 700;
}

/* ================= SEARCH ================= */

.search {
  width: 320px;
  padding: 10px 14px;
  border-radius: 10px;
  border: none;
  background: rgba(255,255,255,0.1);
  color: white;
  margin-bottom: 30px;
}

/* ================= TABLE SECTION ================= */

.table-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 15px;
}

.table-header h2 {
  margin: 0;
  font-size: 22px;
}

.add-btn {
  background: #22c55e;
  border: none;
  padding: 10px 18px;
  border-radius: 10px;
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: 0.2s ease;
}

.add-btn:hover {
  background: #16a34a;
}

/* ================= TABLE CARD ================= */

.card {
  background: rgba(255,255,255,0.05);
  border-radius: 18px;
  padding: 20px;
  backdrop-filter: blur(10px);
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 14px;
  text-align: left;
}

th {
  color: #94a3b8;
  font-weight: 600;
  border-bottom: 1px solid rgba(255,255,255,0.1);
}

td {
  border-bottom: 1px solid rgba(255,255,255,0.05);
}

tr:last-child td {
  border-bottom: none;
}

/* ================= PAGINATION ================= */

.pagination {
  margin-top: 20px;
  display: flex;
  align-items: center;
  gap: 20px;
}

.pagination button {
  padding: 8px 14px;
  border-radius: 8px;
  border: none;
  background: rgba(255,255,255,0.1);
  color: white;
  cursor: pointer;
}

.pagination button:disabled {
  opacity: 0.4;
  cursor: not-allowed;
}

/* ================= MODAL ================= */

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal {
  background: #1e293b;
  padding: 30px;
  border-radius: 16px;
  width: 350px;
}

.modal h2 {
  margin-top: 0;
}

.modal input,
.modal select {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 8px;
  border: none;
  background: rgba(255,255,255,0.1);
  color: white;
}

.modal-buttons {
  display: flex;
  justify-content: space-between;
}

.modal-buttons button {
  padding: 8px 16px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
}

.modal-buttons button:first-child {
  background: #22c55e;
  color: white;
}

.modal-buttons button:last-child {
  background: #ef4444;
  color: white;
}
